<template>
  <div class="chat">
    <!-- 发送内容区域 -->
    <div class="talk_input">
      <!-- 选项: 带有selected的选项,的value值和select标签共享 -->
      <select class="whotalk" id="who" v-model="str1">
        <option value="0">A说：</option>
        <option value="1">B说：</option>
      </select>
      <!-- 请输入内容 -->
      <input type="text" class="talk_word" id="talkwords" v-model="str2" />
      <!-- 按钮 -->
      <input type="button" value="发送" class="talk_sub" id="talksub" @click="add_data()" />
    </div>
    <div class="talk_con2">
      <!-- 显示区域 -->
      <div class="talk_show" id="words" ref="chattingContent">
        <!-- 根据vue对象中的数组，遍历出对应的标签。 -->
        <div v-for="(i,index) in arr" :class="i.person=='A'?'atalk':'btalk'" :key="index">
          <span>{{ i.person }}说：{{ i.words }}</span>
        </div>
      </div>

      <!-- 发送内容区域 -->
      <div class="talk_input">
        <!-- 选项: 带有selected的选项,的value值和select标签共享 -->
        <select class="whotalk" id="who" v-model="str1">
          <option value="0">A说：</option>
          <option value="1">B说：</option>
        </select>
        <!-- 请输入内容 -->
        <input type="text" class="talk_word" id="talkwords" v-model="str2" @keyup.enter="add_data()" />
        <!-- 按钮 -->
        <input type="button" value="发送" class="talk_sub" id="talksub" @click="add_data()" />
      </div>
    </div>
  </div>
</template>

<script>


export default {
  props: {},
  data() {
    return {
      arr: [
        { person: "A", words: "吃饭了吗？" },
        { person: "B", words: "还没呢，你呢？" }
      ],
      str1: "0",
      str2: ""
    };
  },
  methods: {
    add_data: function() {
      // bug: 内容不能为空。
      if (this.str2 == "") {
        alert("请输入内容...");
        return;
      }

      // alert(111)
      // 思路: 页面中遍历一个数组(对象),页面中就会有很多标签。将来添加标签，就是
      // 数组中添加数据。
      //     数组中的数据: 元素:  1.谁说的。   2.说的啥。(描述多条信息，用对象/字典)
      //         数组中放入对象(默认两条数据):    [{}, {}, {person:'A/B', words:'...'}]

      // 点击按钮之后，组成一个元素，放入数组中。
      // alert(this.str1) // 0/1
      // alert(this.str2)
      var p = this.str1 == "0" ? "A" : "B";
      var obj = { person: p, words: this.str2 };
      this.arr.push(obj);

      // 添加完内容，清空
      this.str2 = "";
      setTimeout(() => {
          this.$refs.chattingContent.scrollTop = this.$refs.chattingContent.scrollHeight;
    console.log(this.$refs.chattingContent.scrollTop)
        }, 0);
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.chat{
    font-size: .2rem;
}
.talk_con1 {
  width: 6rem;
  height: 5rme;
  border: 0.01rem solid #666;
  margin: 0.3rem;
  float: left;
  background: #e98080;
}

.talk_con2 {
  width: 6rem;
  height: 5rem;
  border: 0.01rem solid #666;
  margin: 0.3rem;
  float: right;
  background: #7adbd3;
}
.talk_show {
  width:100%;
  height: 10.2rem;
  border: 0.01 solid #666;
  background: #fff;
  margin: 10px auto 0;
  overflow: auto;
}
.talk_input {
  width: 5.8rem;
  margin: 0.1rem auto 0;
}
.whotalk {
  width: 0.8rem;
  height: 0.3rem;
  float: left;
  outline: none;
}
.talk_word {
  width: 4.2rem;
  height: 0.26rem;
  padding: 0rem;
  float: left;
  margin-left: 0.1rem;
  outline: none;
  text-indent: 0.1rem;
}
.talk_sub {
  width: 0.56rem;
  height: 0.3rem;
  float: left;
  margin-left: 0.1rem;
}
.atalk {
  margin: 0.1rem;
}
.atalk span {
  display: inline-block;
  background: #0181cc;
  border-radius: 0.1rem;
  color: #fff;
  padding: 0.05rem 0.1rem;
}
.btalk {
  margin: 0.1rem;
  text-align: right;
}
.btalk span {
  display: inline-block;
  background: #ef8201;
  border-radius: 0.1rem;
  color: #fff;
  padding: 0.05rem 0.1rem;
}
</style>



