<template>
  <div class="chat">
    <Navbar
      :title="title"
      @click-left="onClickLeft"
      :lflag="flag"
      :rightimg="rightimg"
      @ClickRight="onClickRight"
      :rightflag="rightflag"
      @go='goMymess'
    ></Navbar>
    <div v-if="islist">
       
  <GeminiScrollbar class="chatting-content" @resize="fn" :forceGemini=true >
    <div class="chatcontent" ref="chattingContent" id="chattingContent" >
      


      <div class="bodychat" >
        <h1 class="time">{{times}}</h1>
        <div class="content">
          <!-- 有个内容循环 -->
          <div v-for="(item,index) in filterContent()" :key="index">
           
            <div v-if="item.self" class="chatting-item self">
               <span class="verify" v-if="isverify" >!</span>
              <div
                class="msg-content"
                @click="question(item,index)"
                v-html="item.content"
                ref="share1"
              ></div>
             
            </div>

            <div v-else class="chatting-item other">
              <!-- <div class="msg-date">{{ item.date }}</div> -->
              <div class="msg-from">
                <!-- <img :src="item.avatarUrl" alt /> -->
                <img :src="headImg" alt />
                <!-- <span class="msg-author">{{ item.from }}</span> -->
              </div>
              <div
                class="msg-content"
                @click="question(item,index)"
                ref="share1"
                v-html="item.content"
              ></div>
            </div>
          </div>
        </div>
      </div>
    </div>
     
            <!-- <p class="fault" v-if="isfault">你还不是他（她）的好友。请先添加好友请求，对方通过后，才能聊天。
                <span @click="goserchfriend">发送好友请求</span>
              </p> -->
</GeminiScrollbar>
    <div class="bottom"  :class="{'CustomPopupContentShow':showCustomPopup}">
   
    <div class="chatting-input" :class="{android:isandroid}">
      <input @keyup.enter="sendmsg" v-model.trim="inputContent"  />
      <img src="../../../../static/imgs/add.png" alt="" @click="addimg"> <p @click="sendmsg">发送</p>
    </div> 
     <div class="sendImg" v-if="issendimg">
  
       <input type="file" @change.prevent.stop="upload" class="inp" >
  <img src="../../../../static/imgs/picture.png" alt="" icon="photo"> 

     </div>

    </div>
    </div>
     <van-action-sheet v-model="show" :actions="actions" @select="onSelect"  />
      <van-popup v-model="iserr" round class="err">网络连接错误，请检查网络！！</van-popup>

      <van-popup v-model="isfault" round class="fault">
        <p>你还不是他（她）的好友。请先添加好友请求，对方通过后，才能聊天。
                <span @click="goserchfriend">发送好友请求</span>
              </p>
      </van-popup>
     <div class="load" v-if="isload">
        <van-loading type="spinner" color="#1989fa" v-if="isload" />
    </div>
  </div>
</template>
<script>
import qs from "qs";
import Navbar from "../public/navbar";

export default {
  props: {},
  data() {
    return {
