      isverify:null,
      show:false,
      showCustomPopup:false,
      issendimg:false,
      // iscomplaint: false,
      rightimg: "./static/imgs/more.png",
      rightflag: "true",
      title: "",
      // rightimg: "/static/imgs/haoyou.png",
