        (localStorage.msgs_chat && JSON.parse(localStorage.msgs_chat)) || [],
      times: "2019-12 -09",
      subject: {},
      src:"",
      fileName: '',
    isandroid:false
      //user_id: this.$route.params.user_id,
      // isload: true
    };
  },
  watch: {
    msgs(val) {
      // this.filterlist =val.filter(function(item){
      //   return item.userid == this.$store.state.self.userId && item.from_user_id == this.toUserId
      // })
      localStorage.msgs_chat = JSON.stringify(val);
      localStorage.setItem('msgs_chat',JSON.stringify(val))
      // this.msgs = JSON.parse(localStorage.msgs_chat).filter( (va)=>{
      //     //console.log(val)
      //     return va.userid == this.userId && va.from_user_id == this.toUserId
      //   })
      //console.log(val)
    }
  },

  computed: {
    // filterChat(){
    //      return JSON.parse(localStorage.msgs_chat).filter( (val)=>{
    //     //console.log(val)
    //     return val.userid == this.userId && val.from_user_id == this.toUserId
    //   })
    // }
    // message() {
    //   return this.$store.state.message;
    // }
    // fromContent() {
    //   return this.$state.store.fromContent;
    // }
  },
  methods: {
    goserchfriend(){
   this.$router.push('/Searchfriend')
    },
    goMymess(){
//this.$router.go(-1)
if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)[0]==='iPhone;'){
window.webkit.messageHandlers.onClickLeft.postMessage(null)
}else{
  this.$router.go(-1)
}
   
//this.$router.push('/Mymessage')
    },
 fn(val){
console.log(val)
 },
      onSelect(item) {
      this.show = false;
      //console.log(item.name);
     
         this.$router.push({path:`/Complaint/${this.toUserId}`});
        
    
    },
    addimg(){
   this.issendimg = !this.issendimg
   this.showCustomPopup= !this.showCustomPopup;
    },
     upload(event) {
        let files = event.target.files[0];
       // console.log(files)
        this.fileName = this.getObjectUrl(files);
            //console.log(this.getObjectUrl(files))
            this.inputContent = `<img src="${this.fileName}" alt="">`

      this.sendmsg() 
      this.issendimg = false
     this.showCustomPopup= false;

    var formData = new FormData();
      formData.append("user_id", this.$store.state.self.userId);
      formData.append("to_user_id", this.toUserId);
      formData.append("token", this.$store.state.self.token);
      formData.append("number", "1");
      formData.append("picture1", files);
      let config = {
        headers: {
          "Content-Type":
            "multipart/form-data;boundary = " + new Date().getTime()
        }
      };
      this.$axios.post("/picture", formData, config).then(res => {
        console.log(res.data);
        //this.$router.go("-1")
      }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      });
     
    },
    getObjectUrl(file) {
      let url = null;
      if (window.createObjectURL != undefined) {
        // basic
        url = window.createObjectURL(file);
      } else if (window.webkitURL != undefined) {
        // webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      } else if (window.URL != undefined) {
        // mozilla(firefox)
        url = window.URL.createObjectURL(file);
      }
      return url;
    },


    question(item, index) {
      console.log(item.self);
      console.log(index);
      if (item.self) {
        console.log(this.$refs.share1[index].innerText);
        let length =  this.$refs.share1[index].innerText.split("|").length
     
        this.questionId = this.$refs.share1[index].innerText.split("|")[1];
       //console.log(this.questionId)
        let isnum = /^[0-9]+$/.test(this.questionId);
        if (isnum && length>=2) {
          this.$router.push({ path: `/ShareTest/${this.questionId}` });
        }
      } else {
         let length =  this.$refs.share1[index].innerText.split("|").length
        this.questionId = this.$refs.share1[index].innerText.split("|")[1];
          //console.log(this.questionId)
        let isnum = /^[0-9]+$/.test(this.questionId);
        if (isnum && length>=2) {
          this.$router.push({ path: `/ShareTest/${this.questionId}` });
        }
      }
     
    },
   
    // 建立连接放在main
    initWebSocket() {
      if (typeof WebSocket === "undefined") {
        alert("您的浏览器不支持WebSocket");
        return false;
      }
      const wsuri = "ws://cechat.houhang.com:2346"; // websocket地址    ws://im.test:2346
      this.websock = new WebSocket(wsuri);
    },

    onClickRight() {
      console.log("右边事件");
      //this.iscomplaint = true;
      this.show = true;
    },
    onClickLeft() {
      this.$router.go(-1);
    },
     mounted() {
    //  this.oContent = document.querySelector(".chatting-content");
    // this.oContent.scrollTop = this.oContent.scrollHeight;

    this.oContent =document.getElementById("chattingContent")
  this.oContent.scrollTop=this.oContent.scrollHeight
  console.log(this.oContent.scrollTop)
     //console.log(this.oContent.scrollTop)
    //console.log(this.$refs.answer)
    // window.scrollTo(0,0)
    //console.log(this.$route.params);

   
  },
    sendmsg() {
      //this.oContent.scrollTop = this.oContent.scrollHeight;
     

 this.axios
        .post(
         "/chat",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            to_user_id: this.toUserId,
            content: this.inputContent,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res);
          if(res.data.code == 404){
            console.log('报404')
            this.isverify =true
           localStorage.setItem('isverify',this.isverify)
            this.isfault=true;
           // localStorage.setItem('isfault',this.isfault)
            this.$store.commit('changeVerify',this.isverify)
          }
          //console.log(this.inputContent)
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      });




     if (this.inputContent === "") {
        return;
      } else {
        this.msgs.push({
          userid: this.$store.state.self.userId,
          from_user_id: this.toUserId,
          //date: "2019-12-24",
          //loc: localStorage.addr,
          //from: this.fromName,
          content: this.inputContent,
          self: true
          //avatarUrl: this.avatarUrl
        });
        //聊天内容
          this.inputContent = "";
        setTimeout(() => {
          this.$refs.chattingContent.scrollTop = this.$refs.chattingContent.scrollHeight;
    console.log(this.$refs.chattingContent.scrollTop)
        }, 0);

       
        // setTimeout(
        //   () => (this.oContent.scrollTop = this.oContent.scrollHeight),
        //   0
        // );
      }
    },
    //过滤聊天内容
    filterContent(val) {
      return this.msgs.filter(val => {
        //console.log(val)
        return val.userid == this.userId && val.from_user_id == this.toUserId;
      });
    }
  },
beforeCreate() {
   
 
},
  created() {
     this.isverify= localStorage.getItem('isverify');
 // this.isfault = localStorage.getItem('isfault');
  // console.log(this.isverify)
  // console.log(this.isfault)
console.log(JSON.parse(localStorage.getItem('newMsg')))



  if(navigator.userAgent.match(/android/i)){
    this.navigator=navigator.userAgent.match(/android/i)[0]
  // console.log(navigator.userAgent.match(/android/i))
   this.isandroid = true;
    }else{
      console.log('苹果')
    }
 window.addEventListener('scroll',()=>{
        // console.log('滚动条距离顶部'+document.documentElement.scrollTop||document.body.scrollTop);
        // console.log('可视区域'+document.documentElement.clientHeight ||document.body.clientHeight);
        // console.log('滚动条内容的总高度'+document.documentElement.scrollHeight||document.body.scrollHeight);
        let scrollTop = document.documentElement.scrollTop||document.body.scrollTop ;
        let clientHeight = document.documentElement.clientHeight || document.body.clientHeight ;
        let scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight ;
        if(scrollTop+clientHeight == scrollHeight){
            //console.log("到底部了")
           
        }	
    })



    this.initWebSocket();
    this.websock.onopen = res => {
      console.log("已连接");
      setInterval(() => {
        //console.log(this.websock)
        this.websock.send(
          JSON.stringify({
            type: "ping"
          })
        );
      },19000);
    };
 this.websock.onsend=res=>{
   console.log(res.data)
 }
    this.websock.onmessage = res => {
      //接受对方传来的消息
      console.log(res.data);
      this.$store.state.objmsg = JSON.parse(res.data);
      if (this.$store.state.objmsg.type == "init") {
        console.log(this.$store.state.objmsg);
        this.clientId = this.$store.state.objmsg.client_id;
        this.$store.state.self.client_id = this.$store.state.objmsg.client_id;
        this.axios
          .post(
            "/bind",
            qs.stringify({
              user_id: this.$store.state.self.userId,
              client_id: this.$store.state.self.client_id,
              token: this.$store.state.self.token
            })
          )
          .then(res => {
            console.log(res.data);
          }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      });
      } else {
        this.fromContent = this.$store.state.objmsg;
        //console.log(this.fromContent);
        //this.fromName = this.fromContent;
        //console.log(this.fromContent);
        let chatContent = {
          userid: this.$store.state.self.userId,
          from_user_id: this.toUserId,
          content: this.fromContent.content,
          data: this.fromContent.send_time,
          headImg: this.headImg
        };
        //this.msgs.push(this.fromContent,userid);
        this.msgs.push(chatContent);
        //来自对方聊天内容
        //console.log(this.fromContent);
        setTimeout(() => {
          this.oContent.scrollTop = this.oContent.scrollHeight;
        }, 0);
      }
    };
    //console.log(this.$store.state.self.client_id);
    if (this.type === "news") {
       ///获取本地数据
    this.list=   JSON.parse(localStorage.getItem('newMsg'))
    console.log(this.list)
     this.title = this.list[this.nowIndex].username;
          this.toUserId = this.list[this.nowIndex].user_id;
          this.headImg = this.list[this.nowIndex].avatar;
           this.isload=false
        this.islist = true
      // this.axios
      //   .post(
      //    "/all",
      //     qs.stringify({
      //       user_id: this.$store.state.self.userId,
      //       token: this.$store.state.self.token
      //     })
      //   )
      //   .then(res => {
      //     console.log(res.data.data);
      //     this.list = res.data.data;
      //     this.title = this.list[this.nowIndex].username;
      //     this.toUserId = this.list[this.nowIndex].user_id;
      //     this.headImg = this.list[this.nowIndex].avatar;
      //     // this.isload = false;
      //   });
    } else if (this.type === "friend") {
      this.axios
        .post(
         "/entry",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            token: this.$store.state.self.token
            //to_user_id:this.to_user_id
          })
        )
        .then(res => {
          console.log(res.data.data);
          this.list = res.data.data;
          this.title = this.list[this.nowIndex].username;
          this.toUserId = this.list[this.nowIndex].user_id;
          this.headImg = this.list[this.nowIndex].avatar;
           this.isload=false
        this.islist = true
          // this.isload = false;
          //console.log(this.$route.params);
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      });
    } else {
      this.axios
        .post(
         "/entry",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            token: this.$store.state.self.token
            //to_user_id:this.to_user_id
          })
        )
        .then(res => {
          this.list = res.data.data;
          this.title = this.list[this.nowIndex].username;
          this.toUserId = this.list[this.nowIndex].user_id;
          this.headImg = this.list[this.nowIndex].avatar;
           this.isload=false
        this.islist = true
          this.inputContent =
           
          
            "【点击答题】" +
            this.$store.state.self.question.title +
            "<br/>" +
            // `<li v-for="(item,index) in subject" :key="index">{{item.name}}:{{item.desc}}</li>`
            //JSON.stringify(this.$store.state.self.question)

            "A:" +
            this.$store.state.self.question.question.option[0].desc +
            "<br/>" +
            "B:" +
            this.$store.state.self.question.question.option[1].desc +
            "<br/>" +
            "C:" +
            this.$store.state.self.question.question.option[2].desc +
            "<br/>" +
            "D:" +
            this.$store.state.self.question.question.option[3].desc +
          "<span style='color:transparent;'>|"+ this.$store.state.self.question.question_id +"</span>"
          this.sendmsg();
        })
        .then(res => {
          //  this.title = this.list[this.nowIndex].username;
          //     this.toUserId = this.list[this.nowIndex].user_id;
          //     this.headImg = this.list[this.nowIndex].avatar;
          this.inputContent = this.$store.state.newFriend.leaveWord;
          this.sendmsg();
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      });
    }
    
  },
updated() {
  
},
 destroyed() {
    //页面销毁时关闭ws连接
    if (this.websock) {
      this.websock.close(); // 关闭websocket
    }
    },
  components: {
    Navbar
  }
};
