    outline: none;
    border: none;
    position: absolute;
    width: .96rem;
    height: .85rem;
    opacity: 0;
    left: 3rem;
    top: .39rem;
    z-index: 2;
  }
  img{
     position: absolute;
      width: .96rem;
      height: .85rem;
       left: 3rem;
      top: .39rem;
    }
 
}
}
.chatting-input {
  padding-bottom: .35rem;
  width: 7.2rem;
  // position: fixed;
  //bottom: 0;
  //left: 0;
  top:.77rem;//苹果
 position: absolute;
z-index: 22;
  background: rgba(250, 250, 250, 1);
 // padding-bottom: 0.12rem;
  input {
    width: 5.04rem;
    height: .7rem;
    border-radius: 0.2rem;
    outline: none;
    border: none;
    margin-left: 0.32rem;
    font-size: .28rem;
    z-index: 44;
  }
  p {
    margin-top: 0.3rem;
    color: #51e6ef;
    font-size: 0.36rem;
    display: inline-block;
    margin-left: 0.1rem;
    
  }
  img{
    width: .48rem;
    height: .48rem;
    margin-left: .1rem;
    margin-top: -.1rem;
  }
}

.android{
  top:.6rem;
}
.err{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}
 .fault{
   width: 6rem;
   height: 2.2rem;
    padding: .25rem;
   line-height: 1.5;
   font-size: .3rem;
   color: #cccccc;
   word-wrap: break-word;
   span{
     color: #66C1DA;
   }
 }
</style>



