<template>
  <div>
    <div class="search">
       <!-- class="mar_L32" size="16" -->
      <van-icon name="arrow-left" @click="goback" />
      <van-search
        class="ser_friend"
        v-model="value"
        placeholder="输入学员手机号"
        show-action
        shape="round"
        @search="onSearch"
        background="#3fc4e5"
      >
        <div slot="action" @click="onSearch" class="ser_right">搜索</div>
      </van-search>
    </div>
    <ul class="addfriend mar_T12" v-if="unshow">
      <li>
        <img :src="list.headimg" alt class="left" />
        <h1>{{list.name}}</h1>
        <img :src="list.addfriend" alt class="right" @click="show = true" />
      </li>
    </ul>

    <p v-if="isexist" class="hint">该用户不存在</p>
    <van-overlay :show="show" @click="show = false" slot="default">
      <div class="wrapper" @click.stop>
        <div class="block">
          <div class="content">
            <p>
              申请好友：
              <span>{{list.name}}</span>
            </p>
            <p class="explain">申请说明：</p>
            <div class="inp">
 <input type="text" v-model="applyMsg" @keyup.enter="addFriend" />
            </div>
           
            <div class="event">
              <p @click="addFriend">确定</p>
              <p @click="back">取消</p>
            </div>
          </div>
        </div>
      </div>
    </van-overlay>
  <van-popup v-model="isenter" round>已添加，你们已经是好友！！</van-popup>
  <van-popup v-model="iserr" round>网络连接错误，请检查网络！！</van-popup>

  </div>
</template>

<script>
import qs from "qs";

export default {
  props: {},
  data() {
    return {
     iserr:false,
      applyMsg: this.$store.state.newFriend.applyMsg,
      show: false,
      isexist: false,
      value: "",
      rightimg: "./static/imgs/Search.png",
      rightflag: "true",
      lflag: true,
      unshow: false,
      list: { headimg: "", name: "", addfriend: "" },
      //测试
      objlist:[
            {id1:1,id2:2,msg:"1"},
            {id1:1,id2:2,msg:"2"},
            {id1:1,id2:4,msg:"hello3"},
            {id1:2,id2:3,msg:"hello4"},
            {id1:1,id2:2,msg:"hehe5"},
            {id1:2,id2:6,msg:"hello6"}
        ],
        id1:1,
        id2:2,
        friendList:[],
        hadfriend:null,
          isenter:false,
    };
  
  
  },
  computed: {
    // fil(){
    //   return this.objlist.filter(function(val){
    //     return val.id1 == 1 && val.id2 == 2
    //   })
    // }
  },

  methods: {
    addFriend() {
      this.axios
        .post(
          "/out",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            to_user_id: this.$store.state.newFriend.id,
            info: this.applyMsg,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          //console.log(res.data);
          //console.log(this.applyMsg);
          this.$store.commit('changeNewFAMsg',this.applyMsg)
          this.hadfriend = this.friendList.some(this.hasNewFriendId(this.$store.state.newFriend.id))
      if(this.hadfriend){
        this.show = false
        this.isenter= true
          setTimeout(()=>{  
           this.isenter= false
            },1000)
      }else{
this.back();
      }
         
          
        }).catch(err=>{
        //console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      });
    },
    hasNewFriendId(user_id){
      return item =>item.user_id === user_id
    },
    back() {
      this.$router.go(-1);
    },
    onSearch() {
      console.log(this.value);
      //this.phone = this.value
      //this.unshow = true;
      this.list.headimg = "./static/imgs/touxiang.png";
      //this.list.name="张文华"
      this.list.addfriend = "./static/imgs/addfriend.png";
      //这里有api查找用户
      //console.log(this.$store.state.objmsg.client_id)
       this.isload=true
      this.axios
        .post(
         "/user",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            phone: this.value,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data.data);
          if (res.data.code == 200) {
            this.list.headimg = res.data.avatar;
            this.list.name = res.data.data.username;
            this.$store.commit('changeNewFId',res.data.data.id)
            //this.$store.state.newFriend.id = res.data.data.id;
           // console.log("id"+ this.$store.state.newFriend.id)
            //this.to_user_id= res.data.data.id
           // console.log(this.applyMsg);
            this.list.headimg =res.data.data.avatar;

     
            this.unshow = true;
            this.isexist = false;
            this.value = "";
          } else {
            this.unshow = false;
            this.isexist = true;
            this.value = "";
          }
        }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      });
    },
    goback() {
      this.$router.go(-1);
    }
  },
  created() {
      this.axios.post('/entry',qs.stringify({
       user_id:this.$store.state.self.userId,
       token:this.$store.state.self.token
      })).then(res=>{
       // console.log(res.data.data)
        if(res.data.data.length){
         this.friendList = res.data.data
      
          }
      }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      })
  },
  components: {}
};
</script>

<style scoped lang="less">
.search {
  width: 100%;
  height: 0.96rem;
  background-color: #3fc4e5;
  position: relative;
  .ser_friend {
    position: absolute;
  }
  .van-search--show-action {
    margin-left: 0.7rem;
    margin-top: 0.21rem;
    //width: 5.2rem;
  }
  .van-search {
    //width: 5.5rem;
    height: 0.6rem;
  }

  .ser_right {
    font-size: 0.24rem;
    color: #ffffff;
    margin-left: 0.1rem;
    display: block;
  }
  .van-search__action {
    background-color: #3fc4e5;
  }
  .van-search__content--round {
    background-color: #9fe2f2;
    border-radius: 0.3rem;
    width: 5.2rem;
  }
  .van-cell__value {
    margin-top: 0.1rem;
  }

 /*  .van-icon-arrow-left {
    margin-top: 0.12rem;
    position: absolute;
  } */

.van-icon-arrow-left {
  color: #ffffff;
   margin-top: .3rem;
 position: absolute;
 margin-left: .32rem;
}





  .mar_L32 {
    margin-top: 0.35rem;
    color: #ffffff;
  }
  .van-field {
    width: 5.2rem;
  }
  .van-cell{
    color: #ffffff;
  }
}

.addfriend {
  li {
    //display: flex;
    //align-items: center;
  position: relative;
    .left {
      position: absolute;
      width: 0.96rem;
      height: 0.96rem;
      border-radius: 50%;
      margin-left: 0.42rem;
    }
    h1 {
       position: absolute;
      margin-left: 1.55rem;
      color: #000000;
      font-size: 0.36rem;
      font-size: 400;
      top: .17rem;
    }
    .right {
       position: absolute;
      width: 0.56rem;
      height: 0.56rem;
      margin-left: 6.09rem;
      margin-top: .12rem;
    }
  }
}

.hint {
  text-align: center;
  margin-top: 0.5rem;
  font-size: 0.24rem;
  color: #cccccc;
}

//遮罩层样式
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.block {
  width: 5.4rem;
  // height: 5.4rem;
  padding-bottom: .51rem;
  background-color: #fff;
  .content {
    margin-top: 0.42rem;
    margin-left: 0.3rem;
    .explain {
      margin-top: 0.51rem;
      margin-bottom: .2rem;
    }
    .inp{
      border: .01rem solid rgba(242, 242, 242, 1);
    }
    input {
      width: 4.71rem;
      // height: 1.87rem;
      padding-top: .2rem;
      padding-bottom: 1.87rem;
      // border: .01rem solid rgba(242, 242, 242, 1);
      border-radius: .10rem;
      font-size: 0.24rem;
      color: #000000;
      font-weight: 400;
      text-indent: 0.15rem;
      // line-height: 2rem;
      text-align: left;
     word-break: break-all;
    outline-color: transparent;
    outline: none;
    border: none;
    }
  }
  p {
    color: #999999;
    font-size: 0.24rem;
    span {
      font-size: 0.36rem;
      color: #000000;
      font-weight: 600;
    }
  }

  .event {
    
    margin-top: .76rem;
    display: flex;
    margin-left: 0.86rem;
    p:nth-child(1){
       color: #3fc4e5;
    }
    p:nth-child(2) {
      margin-left: 1.88rem;
    }
    p {
      font-size: 0.32rem;
      color: #999999;
    }
    p:hover {
      color: #3fc4e5;
    }
  }
}
.van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}
/* .van-icon-arrow-left:before{
  width: .14rem;
  height: .14rem;

transform: scale(1.7)
} */

.van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}
</style>



