import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let store = new Vuex.Store({
    state:{
        persons:[

        ],
        num:0,
        clientId:"",
        websock: null,
        message:"",
        objmsg:{},
        num:'',
        topHight:null,
        fromContent:{},
        newFriend:{
            id:"",
            applyMsg: "学习的道路上，愿与你同行！",
            leaveWord:"这道题不错分享给你！"
        },
        self:{
            isverify:null,
            token:'',//"7259fa83c2e6de3b73fc8abbacdd2da4"
            userId:"",//46
            client_id:"",
            question:{
            //     question:{
            //    title:''
            //     }
            title:''
            }
        },
        other:{
            userId:"" 
        },
        todos:[
            {id:1,text:'...',done:true},
            {id:2,text:'...',done:false}
        ],
        totalPrice:null
    },
    getters:{
      doneTodos:state=>{
          return state.todos.filter(todo=>todo.done)
      },
      Num:state=>{
          return state.num
      }
    },
   
    mutations:{
        // changeNum(state,payload){
        //     state.totalPrice +=payload.price * payload.count
        // }
        changeNum(state){
            state.num++    
        },
        changeToken(state,token){
            state.self.token = token
        },
        changID(state,userId){
            state.self.userId=userId
        },
        changeQuestion(state,question){
            state.self.question = question
        },
        changeQuestionTitle(state,title){
          state.self.question.title = title
        },
        changeTop(state,topHight){
          state.topHight = topHight
        },
        changeMsg(state,str){
        state.message = str
        },
        changeNewFId(state,id){
            state.newFriend.id = id
        },
        changeNewFAMsg(state,applyMsg){
        state.newFriend.applyMsg= applyMsg
        },
        changeVerify(state,verify){
          state.self.isverify= verify
        }
    },
    actions:{
      changeNum({commit}){
          commit('changeNum')
      }
    }
});

export default store;



