<template>
  <div>
    <Navbar
      :title="title"
      :rightimg="rightimg"
      @ClickRight="onClickRight"
      :rightflag="rightflag"
      :lflag="flag"
      right-text="删除"
    ></Navbar>
    <div class="myfriend" v-if="islist">
      <ul v-for="(item,index) in friendMsg" :key="index">
        <li>
          <van-swipe-cell :border="false">
            <router-link :to='"/Chat/" + "news"+ "/" +index'>
              <van-cell :border="false">
                <template slot="title" :border="false">
                  <img :src="item.avatar" alt class="left" />
                  <div class="center">
                    <div class="content">
                      <h1>{{item.username}}</h1>
                      <div
                        class="right"
                      >{{item.messages[item.messages.length-1].send_time |timefilter}}</div>
                    </div>
                    <p>{{item.messages[item.messages.length-1].content}}</p>
                  </div>
                </template>
              </van-cell>
            </router-link>
            <template slot="right" :border="false">
              <van-button
                square
                color="#FF8282"
                class="del"
                text="删除"
                @click="remove(item.user_id,index)"
                :class="{android:isandroid}"
              />
              <van-button
                square
                color="#F2F2F2"
                text="置顶"
                @click="top(index)"
                :class="{android:isandroid}"
              />
            </template>
          </van-swipe-cell>
        </li>
      </ul>
    </div>
    <div class="load">
      <van-loading type="spinner" color="#3FC4E5" v-if="isload" />
    </div>
    <div class="msg" v-if="ismsg">
      <p>你还没有好友消息，请选择好友聊天！</p>
      <button @click="friend">跳转好友</button>
    </div>

    <!-- <h1>消息{{this.$store.state.self.userId}}</h1> -->
    <!-- <button @click="test1">跳转好友</button> -->
    <!-- <button @click="change">改变状态</button> -->
    <!--   -->
    <!-- <button @click="change">改变状态</button> -->
    <!-- <h1>{{android}}</h1> -->
    <!-- <h1>{{msg1}}</h1>
    <h1>local token:{{token}}</h1>
    <h1>{{navigator}}</h1>
    <button @click="chatpage">聊天框测试</button> -->
    <van-popup v-model="iserr" round>网络连接错误，请检查网络！！</van-popup>
  </div>
</template>

<script>
import qs from "qs";
import Navbar from "../public/navbar";
export default {
  props: {},
  data() {
    return {
      iserr:false,
      navigator:'',
      android:'a',
      ismsg: false,
      isload: true,
      islist: false,
      type: "news",
      id: null,
      title: "我的消息",
      rightimg: "./static/imgs/friend.png",
      rightflag: "true",
      flag: false,
      websock: null,
      //name:'haha',
      obj: {},
     token:'',
      userId:'',
      // messages:[],
      up_user_id: "",
      friendMsg: [],
      //如果List为空跳转到选择好友聊天，如果好友列表为空再跳转到添加好友

      list: [
        {
          //   username: "王文华",
          avatar: "./static/imgs/touxiang.png",
          //   newmsg: "分享试题-【这是题干15个字内展】",
          //   time: "14:53",
          //  user_id:"1",
          messages: [{ send_time: "", content: "" }]
        },
        {
          // username: "张雯雯",
          avatar: "./static/imgs/touxiang.png",
          // newmsg: "好的，我这边看完给你说",
          // time: "12-8",
          // user_id:"2",
          messages: [{ send_time: "", content: "" }]
        }
      ],
      msg1: "未获取到",
      atTop: null,
      type: 1,
      isandroid: false,
      list1: [
        { name: "小张1", age: 11 },
        { name: "小名2", age: 14 }
      ],
      list2: [],
      userId: this.$store.state.self.userId
    };
  },
  watch: {
    friendMsg(val) {
      localStorage.newMsg = JSON.stringify(val);
    }
  },
  filters: {
    timefilter(val) {
      //console.log(this.$moment(val).format('YYYY-DD-MM'))
      console.log(val.slice(-8, -3));
      let timeday = val.slice(5, 10);
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      var nowDate = month + "-" + day;
      if (nowDate == timeday) {
        return val.slice(-8, -3);
      } else {
        return timeday;
      }

      console.log(nowDate);
    }
  },
  //好友消息列表渲染
  methods: {
    chatpage(){
      this.$router.push('testChat')
    },
    friend() {
      this.$router.push("Myfriend");
    },
    change() {
      // this.$store.commit('changeMsg',"119")
      //this.baseURL+
      this.axios
        .post("/userId", {
          token: "514828b7ea8c9807c3ada85dda742309"
        })
        .then(res => {
          console.log(res.data.data);
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      });
    },
    //置顶
    top(index) {
      this.up_user_id = this.friendMsg[index].user_id;
      //console.log("当前人的id:" + this.up_user_id);

      if (index != 0) {
        this.friendMsg.unshift(this.friendMsg.splice(index, 1)[0]);
      }
      //this.baseURL+
      this.axios
        .post(
          "/up",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            up_user_id: this.up_user_id,
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data.data);
          this.atTop = res.data.data.is_top;
        }).catch(err=>{
        //console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      });
    },
    remove(id, index) {
      console.log(id, index);
      this.friendMsg.splice(index, 1);
      let chatMsg = JSON.parse(localStorage.getItem("msgs_chat"));
      //console.log(chatMsg)
      for (let i = chatMsg.length - 1; i > 0; i--) {
        if (chatMsg[i].from_user_id == id && chatMsg[i].userid == this.userId) {
          chatMsg.splice(i, 1);
        }
      }
      localStorage.setItem("msgs_chat", JSON.stringify(chatMsg));
      //console.log(chatMsg)
      // this.axios.post('/allInfo',qs.stringify({
      //    user_id:this.$store.state.self.userId,
      //   to_user_id: id,
      //   token:this.$store.state.self.token
      // })).then(res=>{
      //   console.log(res.data)
      // })
    },
    //ios交互
    callIosFunction(ios) {
    
      this.msg1=ios 
//JSON.parse(ios)
      //this.msg = JSON.parse(ios).token
      this.$store.commit('changeToken',JSON.parse(ios).token)
      localStorage.setItem('token',JSON.parse(ios).token)
       this.axios.post('/userId',{
         token:JSON.parse(ios).token
       }).then(res=>{
       this.userId= res.data.data.user_id
        this.$store.commit('changID',res.data.data.user_id)
       })
.then(()=>{
         this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.msg
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      }).catch(err=>{
        //console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      })
       }).catch(err=>{
        //console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      })


    //this.$store.commit('changeQuestion',JSON.parse(ios))
     
    },
    test1() {
      this.$router.push("SelectFriend");
      // this.axios
      //   .post(
      //     "http://app.houhang.com/index/share/question",
      //     qs.stringify({
      //      question_id: 770,
      //     })).then(res=>{
      //     console.log(res.data)
      //   })
    },

    onClickRight() {
      //alert('跳转我的好友')
      this.$router.push("/Myfriend");
    },
    // test() {
    //   console.log("test");
    //   localStorage.setItem("name", "小智");
    //   this.name = localStorage.getItem("name");

    //   this.$store.commit("changeName", this.name);
    //   localStorage.name = this.name;
    // },
    // remove() {
    //   localStorage.removeItem("name");
    // }

    callJsFunction(android) {
      //this.msg = str;
      //this.msg1 ="改变了"
      this.msg = JSON.parse(JSON.stringify(android)).token;
      this.$store.commit(
        "changeToken",
        JSON.parse(JSON.stringify(android)).token
      );
      //this.baseURL+
      this.axios
        .post("/userId", {
          token: JSON.parse(JSON.stringify(android)).token
        })
        .then(res => {
          // this.userId= res.data.data.user_id
          this.$store.commit("changID", res.data.data.user_id);
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      });
      this.$store.commit("changeQuestion", JSON.parse(JSON.stringify(android)));
      this.topic = this.$store.state.self.question.question.title;
      //this.$store.state.self.question = JSON.parse(JSON.stringify(str))
    }
  },
  beforeCreate() {
    this.friendMsg = JSON.parse(localStorage.getItem("newMsg"));
  },
  created() {
    
    if (navigator.userAgent.match(/android/i)) {
      this.navigator = navigator.userAgent.match(/android/i)[0];
      /// console.log(navigator.userAgent.match(/android/i))
      this.isandroid = true;
    } else {
      console.log("苹果");
    }

    //this.baseURL
       //2.11
    // this.axios
    //   .post(
    //     "/all",
    //     qs.stringify({
    //       user_id: this.$store.state.self.userId,
    //       token: this.$store.state.self.token
    //     })
    //   )
    //   .then(res => {
    //     console.log(res.data.data);
    //     //给list渲染
    //     //this.$store.state.other.userId = res.data

    //     this.list = res.data.data;

    //     if (this.list.length > 0) {
    //       this.isload = false;
    //       this.islist = true;

    //       for (let i = 0; i < this.list.length; i++) {
    //         let flag = true;
    //         for (let j = 0; j < this.friendMsg.length; j++) {
    //           if (this.list[i].user_id == this.friendMsg[j].user_id) {
    //             flag = false;
    //           }
    //         }
    //         if (flag) {
    //           this.friendMsg.push(this.list[i]);
    //         }
    //       }
    //     } else {
    //       this.isload = false;
    //       this.ismsg = true;
    //     }
    //   })
    //   .then(res => {
    //     if (this.islist) {
    //       localStorage.setItem("newMsg", JSON.stringify(this.friendMsg));
    //     } 
    //     if(JSON.parse(localStorage.getItem("newMsg"))){
    //       let len = JSON.parse(localStorage.getItem("newMsg")).length;
    //      if (len > 0) {
    //       this.isload = false;
    //       this.ismsg = false;
    //       this.islist = true;
    //       console.log(JSON.parse(localStorage.getItem("newMsg")));
    //       this.friendMsg = JSON.parse(localStorage.getItem("newMsg"));
    //     }
    //     }
    //     //let len = JSON.parse(localStorage.getItem("newMsg")).length;
    //     //console.log(len)
       
    //   });

    //这里需要加this.baseURL+
    this.axios
      .post("/ban", qs.stringify({
        user_id: this.$store.state.self.userId,
        token: this.$store.state.self.token
      }))
      .then(res => {
        console.log(res.data);
      }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
         // this.isload=true
        this.iserr =true

        }
        
      });
  },
  mounted() {
   
    this.navigator =navigator.userAgent.match(/(iPhone|iPod|iPad);?/i) 
     //window.callIosFunction = this.callIosFunction;
  //this.android = navigator.userAgent.match(/android/i)[0] =='Android'
let token =  localStorage.getItem('token')
 this. navigator= token === this.$store.state.self.token
this.token= token
   console.log(token === null)
   if(token === null){
     if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
 window.callIosFunction = this.callIosFunction;
     }else{
           this.axios
      .post(
        "/all",
        qs.stringify({
          user_id: this.$store.state.self.userId,
          token: this.$store.state.self.token
        })
      )
      .then(res => {
        console.log(res.data.data);
        //给list渲染
        //this.$store.state.other.userId = res.data

        this.list = res.data.data;

        if (this.list.length > 0) {
          this.isload = false;
          this.islist = true;

          for (let i = 0; i < this.list.length; i++) {
            let flag = true;
            for (let j = 0; j < this.friendMsg.length; j++) {
              if (this.list[i].user_id == this.friendMsg[j].user_id) {
                flag = false;
              }
            }
            if (flag) {
              this.friendMsg.push(this.list[i]);
            }
          }
        } else {
          this.isload = false;
          this.ismsg = true;
        }
      })
      .then(res => {
        if (this.islist) {
          localStorage.setItem("newMsg", JSON.stringify(this.friendMsg));
        } 
        if(JSON.parse(localStorage.getItem("newMsg"))){
          let len = JSON.parse(localStorage.getItem("newMsg")).length;
         if (len > 0) {
          this.isload = false;
          this.ismsg = false;
          this.islist = true;
          console.log(JSON.parse(localStorage.getItem("newMsg")));
          this.friendMsg = JSON.parse(localStorage.getItem("newMsg"));
        }
        }
        //let len = JSON.parse(localStorage.getItem("newMsg")).length;
        //console.log(len)
       
      }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      });
     }
     
   }else if(token === this.$store.state.self.token){
    this.axios
      .post(
        "/all",
        qs.stringify({
          user_id: this.$store.state.self.userId,
          token: this.$store.state.self.token
        })
      )
      .then(res => {
        console.log(res.data.data);
        //给list渲染
        //this.$store.state.other.userId = res.data

        this.list = res.data.data;

        if (this.list.length > 0) {
          this.isload = false;
          this.islist = true;

          for (let i = 0; i < this.list.length; i++) {
            let flag = true;
            for (let j = 0; j < this.friendMsg.length; j++) {
              if (this.list[i].user_id == this.friendMsg[j].user_id) {
                flag = false;
              }
            }
            if (flag) {
              this.friendMsg.push(this.list[i]);
            }
          }
        } else {
          this.isload = false;
          this.ismsg = true;
        }
      })
      .then(res => {
        if (this.islist) {
          localStorage.setItem("newMsg", JSON.stringify(this.friendMsg));
        } 
        if(JSON.parse(localStorage.getItem("newMsg"))){
          let len = JSON.parse(localStorage.getItem("newMsg")).length;
         if (len > 0) {
          this.isload = false;
          this.ismsg = false;
          this.islist = true;
          console.log(JSON.parse(localStorage.getItem("newMsg")));
          this.friendMsg = JSON.parse(localStorage.getItem("newMsg"));
        }
        }
        //let len = JSON.parse(localStorage.getItem("newMsg")).length;
        //console.log(len)
       
      }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      });
   }else{
      window.callIosFunction = this.callIosFunction;
   }





    //安卓
    window.callJsFunction = this.callJsFunction;
  },
  computed: {},
  components: {
    Navbar
  }
};
</script>

<style scoped lang='less' >
.msg {
  margin: 1rem auto;
  p {
    font-size: 0.32rem;
    text-align: center;
  }
  button {
    font-size: 0.28rem;
    margin-left: 2.7rem;
    margin-top: 0.1rem;
  }
}
.load {
  margin: 3rem 3rem;
}
.myfriend {
  margin-top: 1.28rem;
  padding-left: 0.32rem;
  ul {
    li {
      display: flex;
      margin-bottom: 0.33rem;
      .van-cell--borderless {
        border: none;
      }
      .left {
        width: 1.1rem;
        height: 1.1rem;
        border-radius: 50%;
      }
      .center {
        padding-bottom: 0.17rem;
        border-bottom: 0.01rem solid rgba(204, 204, 204, 0.4);
        // padding-bottom: .34rem;
        margin-left: 0.25rem;
        display: flex;
        flex-wrap: wrap;
        // font-size: 0;
        //width: 100%;
        .content {
          width: 5.82rem;
          height: 0.34rem;
          //display: flex;
          position: relative;
          display: inline-block;
          h1 {
            margin-left: -0.05rem;
            position: absolute;
            color: #000000;
            font-size: 0.33rem;
            font-weight: 400;
            display: inline-block;
          }
          .right {
            position: absolute;
            right: 0.92rem;
            color: #cccccc;
            font-size: 0.2rem;
            //margin-left: 3.6rem;
            font-weight: 400;
          }
        }
        p {
          color: rgba(204, 204, 204, 1);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          font-size: 0.24rem;
          font-family: "Source Han Sans CN";
          width: 4.42rem;
          margin-top: 0.36rem;
          margin-left: -0.05rem;
          //padding-bottom: .34rem;
          //margin-bottom: .34rem;
        }
      }
    }
  }
}
.van-cell__title {
  display: flex;
}

/* .van-cell{
  border: none;
} */

.del {
  color: #cccccc !important;
}

.van-button--default {
  height: 1.47rem; //苹果
}
.van-swipe-cell__right {
  overflow: hidden;
  //overflow-x: hidden;
  .van-button:nth-child(1) {
    .van-button__text:nth-child(1) {
      color: #ffffff;
    }
  }
}
.van-button__text {
  color: #000000;
  font-size: 0.24rem;
}
.android {
  height: 1.62rem !important;
}
.van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}
</style>



