<template>
  <div>
    <Navbar :title="title" :lflag="lflag"  @go='goMymess'></Navbar>
    <div class="select" v-if="islist">
      <div class="list mar_L32" v-for="(item,index) in list" :key="index" @click="select(index)">
        <!-- 传两个参数一个小标，一个对方ID -->
        <!-- <router-link :to='"/FIntroduce/"+index + "/"+item.user_id'> -->
        <a>
          <img :src="item.avatar" alt />
          <p class="name">{{item.username}}</p>
        </a>
        <!-- </router-link> -->
      </div>
      <!-- 弹窗 -->
      <van-overlay :show="show" @click="show = false" slot="default">
        <div class="wrapper" @click.stop>
          <div class="block">
            <div class="content">
              <p class="share">
                分享给：
                <span>{{nowName}}</span>
              </p>
              <!-- | filtersTopic   过滤 --> 
              <!-- {{this.$store.state.self.question.question.title}} v-html="this.question.title"-->
              <p class="explain"  v-if="isQtitle">{{questionTitle}}</p>
              <input type="text" v-model="leaveWord" @keyup.enter="sendChat" />
              <div class="event">
                <p @click="sendChat" :class="{'active': isactvie}">确定</p>
                <p @click="back" :class="{'active': !isactvie}">取消</p>
              </div>
            </div>
          </div>
        </div>
      </van-overlay>
    </div>
    
    <div class="load">
        <van-loading type="spinner" color="#3FC4E5" v-if="isload" />
    </div>

    <!-- <h1>{{this.$store.state.message}}</h1>
  -->
    <!-- <h1>topic:{{msg}}</h1> -->
      <!-- <button @click="back">按钮</button> -->
    <!-- <h1>{{list}}</h1> -->
       <!-- <h1>msg:{{msg}}</h1>
       <h1>id:{{userId}}</h1> -->

        <div class="msg" v-if="ismsg">
 <p>你还没有好友，请去添加好友！</p>
 <button @click="friend">添加好友</button>
    </div>
    <!-- <h1>loc:{{loc}}</h1>
    <h1>{{token}}</h1>
    <h1>{{userId}}</h1>
    <h1>len:{{len}}</h1> -->

    <!-- <h1>question:{{question}}</h1> -->
    <!-- <h1>question:{{this.questionId}} ={{this.$store.state.self.question.question_id}}</h1>
  <h1>{{list}}</h1> -->
  <!-- <h1>{{textQ | formatQuestion}}</h1> -->
  <!-- <h1>{{text}}</h1> -->
  <!-- <h1>{{this.$store.state.self.question.question_id}}</h1> -->
  
  <!-- <h1>loc:{{loc}}</h1> -->
  <!-- <h1>{{questionId}}</h1> -->
  <van-popup v-model="iserr" round>网络连接错误，请检查网络！！</van-popup>
  </div>
</template>
<script>
import qs from "qs";
import Navbar from "../public/navbar";
// import Load from "../public/load";
export default {
  props: {},
  data() {
    return {
      iserr:false,
      isQtitle:false,
       ismsg:false,
        isload:true,
        islist:false,
      nowindex: null,
      leaveWord: this.$store.state.newFriend.leaveWord,
      topic:'',
      show: false,
      nowName: "",
      title: "我的好友",
      lflag: true,
      question: null,
      list: [{username:"",avatar:""}],  ///static/imgs/touxiang.png
      msg: "哈哈",
      userId:this.$store.state.self.userId,
      token:this.$store.state.self.token,
     isactvie:true,
     textQ:'在建设设计中，体现出来 （&amp;amp;amp;amp）',
     text:"",
     questionTitle:'',
     loc:null,
      //obj: '',
      // questionId:this.$router.params
    };
  },
  methods: {
     goMymess(){
//this.$router.go(-1)
 window.webkit.messageHandlers.onClickLeft.postMessage(null)

},
    friend(){
   this.$router.push('Searchfriend')
    },
    select(index) {
      console.log(index);
      this.nowindex = index
      this.nowName = this.list[index].username;
      this.show = true;
    },
    sendChat() {
        this.$router.push({name:'Chat',params:{type:"selectfriend",index:this.nowindex}})
      //console.log("确定测试");
      //this.back();
    },
    back() {
     this.show = false
     this.isactvie=!this.isactvie
     //this.$router.push('/SelectFriend')
      //console.log("返回测试");
    },

     //h5和安卓交互
    callJsFunction(android) {
      //this.msg = str;
      //this.msg1 ="改变了"
      this.msg = JSON.parse(JSON.stringify(android)).token
      this.$store.commit('changeToken',JSON.parse(JSON.stringify(android)).token)
//this.baseURL+
       this.axios.post('/userId',{
         token:JSON.parse(JSON.stringify(android)).token
       }).then(res=>{
        this.userId= res.data.data.user_id
        this.$store.commit('changID',res.data.data.user_id)
       }).catch(err=>{
        //console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      })
      this.$store.commit('changeQuestion',JSON.parse(JSON.stringify(android)))
        this.topic = this.$store.state.self.question.question.title
      //this.$store.state.self.question = JSON.parse(JSON.stringify(str))
   
     },
      //ios交互
    callIosFunction(ios) {
    
      //this.msg=ios.token
//JSON.parse(ios)
      this.token = JSON.parse(ios).token
      localStorage.setItem('token',JSON.parse(ios).token)
      this.$store.commit('changeToken',JSON.parse(ios).token)
      localStorage.setItem('token',JSON.parse(ios).token)
       this.$store.commit('changeQuestion',JSON.parse(ios))
       // this.$store.commit('changeQuestion',JSON.parse(ios))
      localStorage.setItem('questionId',JSON.parse(ios).question_id)
       this.axios.post('/userId',{
         token:JSON.parse(ios).token
       }).then(res=>{
          this.question = this.$store.state.self.question.question
           this.questionTitle = this.question.title.split('（')[0] + "（ ）"
           this.$store.commit('changeQuestionTitle',this.questionTitle)
          this.isQtitle= true
       this.userId= res.data.data.user_id
        this.$store.commit('changID',res.data.data.user_id)
      
       })
.then(()=>{
   
         this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.token
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     this.len = this.list.length +'g d'
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      })
       }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      })


   
     
    }
    
  },
  watch: {
    obj(val) {
      this.obj = val;
      //console.log(this.obj);
    }
  },
  created() { 
     this.questionId =localStorage.getItem('questionId')

  },
  filters:{
    filtersTopic(val){
        return val.substring(1,val.length-1)
    }
  },
  mounted() {
   // let token =  localStorage.getItem('token')
 //window.callIosFunction = this.callIosFunction
  
   //console.log(this.questionId === null)
   
   //console.log(token === this.$store.state.self.token)
   if(this.questionId === null){
     if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
    window.callIosFunction = this.callIosFunction;
     }else{
           this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.token
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      })
     }
   }else if(this.questionId == this.$store.state.self.question.question_id){
     this.loc= this.questionId == this.$store.state.self.question.question_id

    this.question = this.$store.state.self.question.question
    this.questionTitle = this.question.title.split('（')[0] + "（ ）"
    
    this.isQtitle= true
    this.axios.post('/entry',qs.stringify({
      user_id: this.$store.state.self.userId,
      token: this.$store.state.self.token
      })).then(res=>{
        //console.log(res.data.data)
        this.list = res.data.data
     //this.len = this.list.length
      if(this.list.length > 0){
          this.islist = true
          this.isload=false
          
          }else if(this.len.length <= 0){
              this.isload=false 
            this.ismsg= true
          }
      }).catch(err=>{
       // console.log(err.response.status)
        if(err.response.status == 500){
          //this.isload=true
        this.iserr =true

        }
        
      })
     
   }else if(this.questionId != this.$store.state.self.question.question_id){
      window.callIosFunction = this.callIosFunction;
   }


    //安卓
    window.callJsFunction = this.callJsFunction;
     //console.log(window.callJsFunction)
  },
  components: {
    Navbar,
    
  }
};
</script>

<style scoped lang="less">

.msg{
 margin:1rem auto;
  p{
    font-size: .32rem;
    text-align: center;
  }
  button{
   
    font-size: .28rem;
    margin-left: 2.7rem;
    margin-top: .1rem;
  }
}
.select {
  margin-top: 1.38rem;
  .name {
    margin-left: 0.36rem;
    padding-top: 0.28rem;
    padding-bottom: 0.28rem;
    color: #000000;
    font-size: 0.36rem;
    font-weight: 400;
  }
  .list {
    a {
      display: flex;
      margin-bottom: 0.24rem;
    }
    img {
      width: 0.96rem;
      height: 0.96rem;
      border-radius: 50%;
    }
  }

  //遮罩层样式
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .block {
    width: 5.4rem;
    //height: 4.38rem;
    background-color: #fff;
    .content {
      margin-top: .32rem;
      margin-left:.27rem;
      margin-right: .18rem;
      .share{
          font-size: .32rem;
          color: #999999;
          span{
              color: #000000;
          }
      }
      .explain {
          padding: .28rem .31rem .34rem .17rem;
          margin-top: .42rem;
          margin-left:0;
          margin-right: .18rem;
      
        font-size: .28rem;
        color: #000000;
        background-color: #F2F2F2;
        width: 4.95rem;
        //height: 1.23rem;
        //line-height: 1.23rem;
        //text-align: center;
      }
      input {
          margin-top: .48rem;
          width: 4.9rem;
        
 
        // height: .39rem;
        //  border: .01rem solid rgba(242, 242, 242, 1);
        //border-radius: 10px;
        font-size: .24rem;
        color: #999999;
        font-weight: 400;
        text-indent: .15rem;
        // line-height: .23rem;
      border: none;
        outline: transparent;
      }
    }


    .event {
      margin-top: .62rem;
      margin-bottom: .48rem;
     margin-left: .94rem;
      display: flex;
      color: #999999;
      
      p:nth-child(2) {
        margin-left: 1.88rem;
      }
      p {
        font-size: 0.32rem;
       
      }
      /* p:hover {
        color: #3fc4e5;
      } */
    }
  }
}
.load{
    margin: 3rem 3rem;
}
.active{
          color: #3fc4e5;
      } 

      .van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
} 
.van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}

</style>



