<template>
  <div>
    <Navbar :title="title" :lflag="lflag" @go="goMymess"></Navbar>
    <ul class="mar_L32 mar_T12">
        <!-- 到搜索好友界面 -->
      <li @click="addFriend">
        <img src="../../../../static/imgs/addfriend.png" alt />
        <p>添加好友</p>
      </li>
      <!-- 到新的好友界面，点进去加载考虑 -->
      <li>
        <img src="../../../../static/imgs/newfriend.png" alt />
        <p @click="jump">新的好友</p>
      </li>
    </ul>
    <div class="friendlist" v-if="islist">
        <!-- 我的所有好友放x? 需要再mounted里面-->
        <!-- <h1>{{msg}}</h1> -->
        <p>好友列表</p>
        <!-- 路由跳转到好友介绍 -->
        <div class="list mar_L32" v-for="(item,index) in list" :key="index" >
           <router-link :to='"/FIntroduce/"+index +"/"+ item.user_id'>
            <img :src="item.avatar" alt="">
            <p>{{item.username}}</p>
            </router-link>
        </div>
    </div>
   <div class="load">
      <van-loading type="spinner" color="#3FC4E5" v-if="isload" />
   </div>
   <div class="msg" v-if="ismsg">
 <p>你还没有好友，请去添加好友！</p>
 <button @click="friend">添加好友</button>
    </div>
     
      <van-popup v-model="iserr" round>网络连接错误，请检查网络！！</van-popup>
    <!-- <h1>ios:{{this.$store.state.self.token}} </h1>
    <h1>{{this.$store.state.self.userId}}</h1>
    <h1>{{this.list}}</h1> -->
  
  </div>
</template>

<script>
import qs from "qs";
import Navbar from "../public/navbar";
import { mapState } from 'vuex'
import { mapMutations } from 'vuex'
export default {
  props: {},
  data() {
    return {
       iserr:false,
       ismsg:false,
      isload:true,
      islist:false,
      msg:"没传过来",
      question:"前输入文字",
      title: "我的好友",
      lflag:true,
      list:[
           {username:"",avatar:""}   ///static/imgs/touxiang.png
         
      ],
      userId:this.$store.state.self.userId,
      token:this.$store.state.self.token
    };
  },
  watch: {
    // msg(){
    //   this.question ="正在输入"
    // }
  },
  beforeCreate() {
    //this.$router.go(0)
  },
  created() {
  
//     this.axios.post('/entry',qs.stringify({
//        user_id:this.userId,
//        token: this.token
//       })).then(res=>{
//         console.log(res.data.data)
//         this.list = res
     
//      if(this.list.length>0){
//           this.isload=false
//           this.islist = true
//           }else{
//               this.isload=false
//             this.ismsg= true
//           }
//       }).catch(err=>{
//         console.log(err.response.status)
//         if(err.response.status == 500){
// this.iserr =true
// this.isload=true
//         }
        
//       })

    // console.log(this.$store.state)
      
  },
  mounted() {
     //ios
    //window.callIosFunction = this.callIosFunction;

   let token =  localStorage.getItem('token')
   console.log(token === null)
  
   if(token === null){
     if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
 window.callIosFunction = this.callIosFunction;
 console.log(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i))
     }else{
           this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.token
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
          this.isload=true
        this.iserr =true

        }
        
      })
     }
     
   }else if(token ===this.$store.state.self.token){
    this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.token
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
this.iserr =true
this.isload=true
        }
        
      })
   }else{
      window.callIosFunction = this.callIosFunction;
   }
  },
  computed:mapState([
    // num(){
    //   return this.$store.state.totalPrice
    // }
     'totalPrice',
     'num'
  ]),
  methods: {
    //ios交互
    callIosFunction(ios) {
    
      this.msg=ios 
//JSON.parse(ios)
      //this.msg = JSON.parse(ios).token
      this.$store.commit('changeToken',JSON.parse(ios).token)
      localStorage.setItem('token',JSON.parse(ios).token)
       this.axios.post('/userId',{
         token:JSON.parse(ios).token
       }).then(res=>{
       this.userId= res.data.data.user_id
        this.$store.commit('changID',res.data.data.user_id)
       })
.then(()=>{
         this.axios.post('/entry',qs.stringify({
       user_id:this.userId,
       token: this.msg
      })).then(res=>{
        console.log(res.data.data)
        this.list = res.data.data
     
     if(this.list.length>0){
          this.isload=false
          this.islist = true
          }else{
              this.isload=false
            this.ismsg= true
          }
      })
       }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
this.iserr =true
this.isload=true
        }
        
      })


    //this.$store.commit('changeQuestion',JSON.parse(ios))
     
    },
    // test(index){
    //  this.$router.push({path:`/FIntroduce/${index}`})
    // },
     friend(){
   this.$router.push('Searchfriend')
    },
    goMymess(){
  //this.$router.go(-1)
       //window.webkit.messageHandlers.onClickLeft.postMessage(null)
//this.$router.push('/Mymessage')
  //[0] === 'iPhone;'
if(navigator.userAgent.match(/(iPhone|iPod|iPad);?/i)){
   window.webkit.messageHandlers.onClickLeft.postMessage(null)
   //[0] =='Android'
    }else if(navigator.userAgent.match(/android/i)){
    //window.androidDelegate.getCall()
    this.$router.go(-1)
    }else{
this.$router.go(-1)
    }
    },
      test1(){
  this.axios
        .post(
          "/blackList",
          qs.stringify({
            user_id: this.userId,
            //del_user_id: this.$route.params.userId,
            token: this.msg
          })
        )
        .then(res => {
          console.log(res.data);
     
        }).catch(err=>{
        console.log(err.response.status)
        if(err.response.status == 500){
this.iserr =true
this.isload=true
        }
        
      });
    },
       test2(){
  this.axios
        .post(
          "/black",
          qs.stringify({
            user_id: this.$store.state.self.userId,
            //del_user_id: this.$route.params.userId,
            to_user_id:"238",
            token: this.$store.state.self.token
          })
        )
        .then(res => {
          console.log(res.data);
     
        });
    },
    ...mapMutations([
       "changeNum"
    ]),
    ...mapMutations({
   add:'changeNum'
    }),
    btn(){
    //  this.$store.commit({
    //    type:"changeNum",
    //    price:10,
    //    count:2
    //  })
    this.$store.dispatch('changeNum')
     console.log(this.$store.getters.doneTodos)
    },
      test(){
       this.$router.push("SelectFriend")
      },
      addFriend(){
          this.$router.push('/Searchfriend')
      },
      jump(){
        this.$router.push('/NewFriend')
      }
  },

  

  components: {
    Navbar
  }
};
</script>

<style scoped lang="less">
.msg{
  margin:1rem auto;
  p{
    font-size: .32rem;
    text-align: center;
  }
  button{
   
    font-size: .28rem;
    margin-left: 2.7rem;
    margin-top: .1rem;
  }
}
.load{
   margin: 3rem 3rem
}
ul{
   margin-top: 1.28rem;
    li{
        display: flex;
        margin-bottom: .24rem;
        
        img{
            width: .96rem;
            height: .96rem;
           
        }
        p{
            color: #000000;
            font-size: .36rem;
            margin-left: .25rem;
            margin-top: .31rem;
        }
    }
}
.friendlist{
    margin-top: .28rem;
    p{
        margin-left: .36rem;
        padding-top:.28rem;
        padding-bottom: .28rem;
        color: #000000;
        font-size: .36rem;
        font-weight: 400;
    }
    .list{
        
        a{
        display: flex;
        margin-bottom: .24rem;
        }
        img{
            width: .96rem;
            height: .96rem;
            border-radius: 50%;
        }
    }
}
.van-popup{
    width: 5.4rem;
    height: 1.56rem;
    text-align: center;
    line-height: 1.56rem;
    color: #000000;
    font-size: .32rem;
}
</style>



